<template>
    <div class="listing mover-panel" :id="targetId"> 
           <div class="border-0 p-0 d-flex flex-wrap"> 
                <div class="mover-panel-box leftpanel" id="MoverLeft"> 
                  <div class="heading-border"> 
                      <h5 class="h5"><span>{{titleLeft}}</span></h5> 
                  </div> 
                  <div class="col-12 pl-0 mt-2 px-small-0"> 
                     <div class="col-sm-12 col-md-8 col-lg-8 float-left pr-1 pl-0 px-small-0"> 
                         <div class="form-group mb-1"> 
                              <input id="search" maxlength="50" v-model="Search" class="form-control" type="text" v-on:keyup="SearchData()" placeholder="Search by Name" autocomplete="off"> 
                         </div> 
                   </div> 
                     <div class="col-sm-12 col-md-4 col-lg-4 float-left pl-0 px-small-0"> 
                           <div class="search-btm-btn background-0 text-left form-check-inline"> 
                              <a :title="$t('Search')" href="javascript:;" class="btn btn-success mr-1 px-4" v-on:click="SearchData()"><em class="fa fa-search"></em></a> 
                            <a href="javascript:;" :title="$t('Clear')" class="btn btn-danger clearsearchtext px-4"  v-on:click="ClearData()"><em class="fa fa-refresh"></em></a> 
                           </div> 
                       </div> 
                   </div> 
                <div class="listing mt-2" > 
                <vue-scroll>
                   <div class="table-responsive scrollmoverdiv" style="max-height:500px;"> 
                           <table class="table table-bordered" aria-describedby="fildname"> 
                               <thead class="thead-bg"> 
                                   <tr role="row"> 
                                       <th scope="col">{{$t("fieldName") }}</th> 
                                </tr> 
                               </thead> 
                                <tbody :id="targetId + 'LeftItems'" > 
                                    <tr  v-for="(item,index) in unselectedItems" v-bind:class="{'low-bar': item.isSelected,'even':index%2==2, 'odd':index%2>0}" :Key="item"  v-on:click="selectItem(item, unselectedItems)" v-bind:data-id="item.value"  data-side="left"  v-bind:data-index="index"> 
                                        <td class="sorting_1">{{item.displayValue}}</td> 
                                    </tr> 
                                    <tr v-if="unselectedItems== null || unselectedItems.length == 0"  style="cursor:initial;"> 
                                       <td class="text-center text-danger"> {{$t("NoRecordfound")}}</td> 
                                   </tr> 
                               </tbody> 
                           </table> 
                        </div>
                    </vue-scroll> 
                   </div> 
             </div> 
              <div class="mover-controls"> 
                  <button type="button" data-toggle-tooltip="tooltip" :title="$t('MoveAllRight')" v-on:click="moveAllRight()" v-if="moveAll != 'false'"><em aria-hidden="true" class="fa fa-angle-double-down fa-2x d-md-none d-none "></em><em aria-hidden="true" class="fa fa-angle-double-right fa-2x d-none d-md-block d-block"></em></button> 
                   <button type="button" data-toggle-tooltip="tooltip" :title="$t('MoveRight')" v-on:click="moveRight()"><em aria-hidden="true" class="fa fa-angle-down fa-2x d-md-none d-none"></em><em aria-hidden="true" class="fa fa-angle-right fa-2x d-none d-md-block d-block"></em></button> 
                   <button type="button" data-toggle-tooltip="tooltip" :title="$t('MoveLeft')" v-on:click="moveLeft()"><em aria-hidden="true" class="fa fa-angle-up fa-2x d-md-none d-none"></em><em aria-hidden="true" class="fa fa-angle-left fa-2x d-none d-md-block d-block"></em></button> 
                  <button type="button" data-toggle-tooltip="tooltip" :title="$t('MoveAllLeft')" v-on:click="moveAllLeft()" v-if="moveAll != 'false'"><em aria-hidden="true" class="fa fa-angle-double-up fa-2x d-md-none d-none"></em><em aria-hidden="true" class="fa fa fa-angle-double-left fa-2x d-none d-md-block d-block"></em></button> 
              </div> 
              <div class="mover-panel-box rightpanel" id="MoverRight"> 
                  <div class="heading-border"> 
                       <h5 class="h5"><span>{{titleRight}}</span></h5> 
                  </div>  
                  <div class="col-12 pl-0 mt-2 d-none"> 
                       <div class="col-4 float-left pr-1 pl-0"> 
                          <div class="form-group mb-0"> 
                              <input id="search" maxlength="50" class="form-control" type="text" placeholder="Search By Title" autocomplete="off"> 
                          </div> 
                       </div> 
                       <div class="col-4 float-left pr-1 pl-0"> 
                           <div class="form-group mb-0"> 
                             <input id="search" maxlength="50" class="form-control" type="text" placeholder="Search By Title" autocomplete="off"> 
                          </div> 
                      </div> 
               <div class="col-4 float-left pl-0"> 
                         <div class="search-btm-btn background-0 text-left form-check-inline"> 
                              <a title="" id="Go" href="javascript:;" class="btn btn-success mr-1 px-4" data-toggle-tooltip="tooltip" data-original-title="Search"><em class="fa fa-search"></em></a> 
                             <a href="javascript:;" title="" class="btn btn-danger clearsearchtext px-4" data-toggle-tooltip="tooltip" data-original-title="Clear"><em class="fa fa-refresh"></em></a> 
                          </div> 
                       </div> 
               </div>            
               <div class="listing mt-2" > 
                <vue-scroll>
                    <div class="table-responsive scrollmoverdiv" style="max-height:500px;"> 
                        <table id="example" class="table table-bordered" aria-describedby="mover"> 
                            <thead class="thead-bg"> 
                                 <tr> 
                                        <th scope="col"  > <span> {{$t("fieldName")}} </span></th> 
                                 <th scope="col"   class="text-center" style="width:100px;"> {{$t("Sortable")}}</th> 
                                 </tr> 
                            </thead> 
                              <tbody :id="targetId + 'RightItems'" >
                                  <tr v-for="(item,index) in selectedItems"  v-bind:class="{'even':index%2==2,'odd':index%2>0 , 'low-bar': item.isSelected}"  v-on:click="selectItem(item, selectedItems)" v-bind:data-id="item.value" data-side="right" v-bind:data-index="index" :Key="item"> 
                                      <td><span class="my-handle">{{item.displayValue}}</span></td> 
      
                                     <td class="text-center"><div class="custom-control custom-checkbox"><input v-bind:disabled="item.isSortableEnabled == 0" v-bind:id="item.value" class="form-check-input float-right custom-control-input multilevel" v-model="item.isSortable" v-bind:name="item.value" v-bind:value="item.isSortable" style="display: none;" type="checkbox"><label class="custom-control-label universal-custom-control-label pt-1" v-bind:for="item.value"></label></div></td> 
                                  </tr> 
                                   <tr v-if="selectedItems== null || selectedItems.length == 0"  style="cursor:initial;" > 
                                  <td class="text-center text-danger" colspan="2"> {{$t("NoFieldSelected")}}</td> 
                                   </tr> 
                                </tbody> 
                           </table> 
                       </div> 
                    </vue-scroll>
                   </div> 
               </div> 
        </div> 
       </div>
</template>

<script>
import Sortable from 'sortablejs';
export default {
     props: {
     
        titleLeft: {
            type: String,
            default: 'Available'
        },
       
        titleRight: {
            type: String,
            default: 'Selected'
        },
    
        movedItemLocation: {
            type: String,
            default: "top"
        },
       
        leftItems: Array,
     
        rightItems: Array,
     
        targetId: {
            type: String,
            default: "Mover"
        },
        
        normalizeLists: {
            type: Boolean,
            default: true
        },
        fontAwesome: {
            type: Boolean,
            default: true
        },
        moveAll: {
            type: Boolean,
            default: true
        },
        maxItem: {
            type: String,
            default: null
        }
    },
    methods: {
       
        raiseItemMoved: function _raiseItemMoved(item, targetList, listType) {
            this.lastMovedItem = { item: item, targetList: targetList, listType: listType };
            this.$emit('item-moved', this.lastMovedItem);
        },
    },
     data: function () {
        var vm = {
            selectedSortable: null,
            unselectedSortable: null,
            selectedItem: {},
            selectedList: null,
            selectedItems: this.rightItems,
            unselectedItems: [],
            AllItems:[],
            lastMovedItem: null,
             Search: '',

          
            initialize: function (vue) {

                var options1 = {
                    group: "_mvgp_" + new Date().getTime(),
                    ghostClass: "mover-ghost",
                    chosenClass: "mover-selected",
                    onAdd: vm.onListDrop,
                    onUpdate: vm.onSorted,
                    sort: false, 
                    disabled: true
                };

                var options2 = {
                    group: "_mvgp_" + new Date().getTime(),
                    ghostClass: "mover-ghost",
                    chosenClass: "mover-selected",
                    onAdd: vm.onListDrop,
                    onUpdate: vm.onSorted,
                    handle: ".my-handle",
                    nodeName: 'tr'
                };

                var targetId = vue.targetId;
                var el = document.getElementById(targetId + 'LeftItems');
                 vm.unselectedSortable = Sortable.create(el, options1);

                var el2 = document.getElementById(targetId + 'RightItems');
                 vm.selectedSortable = Sortable.create(el2, options2);

                if (vue.normalizeLists)
                    vm.normalizeListValues();
            },
            selectItem: function (item, items) {
                var vm=this;              
                if (item.hasOwnProperty('length')) {
                    if (items.length > 0)
                        item = items[0];
                    if (!item) return;
                }else{
                item.isSelected = !item.isSelected;
                vm.selectedItem = item;
                vm.selectedList = items;
                }            
            },
            moveRight: function (item, index) {
                var vm=this;
                if (!item) {
                    var item = $.grep(vm.unselectedItems, function (itm, index) {
                        return itm.isSelected;
                    });
                }
                if (!item)
                    return;
                if (vm.selectedItems.length > (vm.maxItem - 1) && vm.maxItem > 0) {                    
                    vm.ShowAlert(vm.$t(('SelectFieldMessage', [vm.$t(vm.maxItem)])), "warning", true, vm.$t('Alert'));
                    return;
                }
                if ((item.length + vm.selectedItems.length) > vm.maxItem && vm.maxItem > 0) {
                    vm.ShowAlert(vm.$t('SelectFieldMessage',[vm.$t(vm.maxItem)]), "warning", true, vm.$t('Alert'));
                    return;
                }                
                $.each(item, function (index, itm) {
                    itm.isSelected = false;
                    var idx = vm.unselectedItems.indexOf(itm);
                    vm.unselectedItems.splice(idx, 1);
                    if (typeof index === "number")
                        vm.selectedItems.splice(index, 0, itm);
                    else {
                        if (vm.movedItemLocation == "top")
                            vm.selectedItems.unshift(itm);
                        else {
                            vm.selectedItems.push(itm);
                            var container = vm.$el.querySelector(".mover-right>.mover-panel");
                            setTimeout(function () {
                                container.scrollTop = container.scrollHeight;
                            });
                        }
                    }
                });
                setTimeout(function () {
                    vm.selectItem(item, vm.selectedItems);
                    vm.raiseItemMoved(item, vm.selectedItems, "right");
                }, 1);
                vm.SetCheckbox("right",vm);
            },
            moveLeft: function () {
                var vm=this;
                 var item = $.grep(vm.selectedItems, function (itm, index) {
                    return itm.isSelected;
                });

                if (!item)
                    return;
                $.each(item, function (index, itm) {
                    itm.isSelected = false;
                    var idx = vm.selectedItems.indexOf(itm);
                    vm.selectedItems.splice(idx, 1);
                    if (typeof index === "number")
                        vm.unselectedItems.splice(index, 0, itm);
                    else {
                        if (vm.movedItemLocation == "top")
                            vm.unselectedItems.unshift(itm);
                        else {
                            vm.unselectedItems.push(itm);
                            var container = vm.$el.querySelector(".mover-left>.mover-panel");
                            setTimeout(function () { container.scrollTop = container.scrollHeight; });
                        }
                    }

                });
                setTimeout(function () {
                    vm.selectItem(item, vm.unselectedItems);
                    vm.raiseItemMoved(item, vm.unselectedItems, "left");
                }, 10);
                vm.SetCheckbox("left",vm);
            },
            SetCheckbox: function (type,vm) {
                setTimeout(function () {
                    vm.CheckBoxBootstrap();
                    //$(".mover-panel :checkbox").prop("checked", false);
                    $(".mover-panel :checkbox").prop("checked", true);
                }, 100);
            },
            moveAllRight: function () {
                var vm = this;
                for (var i = vm.unselectedItems.length - 1; i >= 0; i--) {
                    var item = vm.unselectedItems[i];
                    vm.unselectedItems.splice(i, 1);
                    vm.selectedItems.push(item);
                }
              
            },
            moveAllLeft: function () {
                var vm = this;
                for (var i = vm.selectedItems.length - 1; i >= 0; i--) {
                    var item = vm.selectedItems[i];
                    vm.selectedItems.splice(i, 1);
                    vm.unselectedItems.push(item);
                }
            },
            refreshListDisplay: function () {
                setTimeout(function () {
                    var list = vm.selectedItems;
                    vm.selectedItems = [];
                    vm.selectedItems = list;

                    list = vm.unselectedItems;
                    vm.unselectedItems = [];
                    vm.unselectedItems = list;
                }, 10);
            },
            SearchData: function () {
            
                var vm=this;
                var search = vm.Search;
                search = search.toLowerCase();
                vm.unselectedItems = vm.AllItems.filter(function (val) {
                    return vm.selectedItems.filter(function (x) { return x.value == val.value }).length == 0;
                });
                vm.unselectedItems = $.grep(vm.unselectedItems, function (ele) {
                    return ele.displayValue.toLowerCase().indexOf(search) != -1;
                });
            },
            ClearData: function () {
              
                var vm=this;
                vm.Search = '';
                vm.unselectedItems = vm.AllItems.filter(function (val) {
                    return vm.selectedItems.filter(function (x) { return x.value == val.value }).length == 0;
                }); 
            },
            onSorted: function (e) {
            
                var key = e.item.dataset["id"];
                var side = e.item.dataset["side"];
                var ItemIndex = e.item.dataset["index"];
                if (side == "left") {
                    var list = vm.SortItemOfList(vm.unselectedItems, key, ItemIndex, e.newIndex);
                    vm.unselectedItems = [];
                    setTimeout(function () {
                        vm.unselectedItems = list;
                    }, 1);
                }
                else {
                    list = vm.SortItemOfList(vm.selectedItems, key, ItemIndex, e.newIndex);
                    vm.selectedItems = [];
                    setTimeout(function () {
                        vm.selectedItems = list;
                    }, 1);

                }
            },
            SortItemOfList: function (list, key, ItemIndex, newIndex) {
                var item = list.find(function (itm) {
                    return itm.value == key;
                });
                if (!item)
                    return list;
                list.splice(ItemIndex, 1);

                list.splice(newIndex, 0, item);
                list.forEach(function (itm) {
                    itm.isSelected = false;
                });
                vm.selectItem(item, list);
                return list;
            },
            onListDrop: function (e) {
              return false;
        },
            normalizeListValues: function () {
                if (!vm.selectedItems || vm.selectedItems.length == 0 ||
                    !vm.unselectedItems || vm.unselectedItems.length == 0)
                    return;

                for (var item of vm.selectedItems) {
                    var selected = item;
                    var idx = vm.unselectedItems.findIndex(function (itm) {
                        return itm.value == selected.value;
                    });
                    if (idx > -1)
                        vm.unselectedItems.splice(idx, 1);
                }
            }
        }

        var vue = this;
        setTimeout(function () { vm.initialize(vue); }, 100);
        return vm;
    },
    created: function () {
        var obj = this;
        setTimeout(function () {
            //obj.AllItems = JSON.parse(JSON.stringify(obj.leftItems));
            var leftObj = JSON.parse(JSON.stringify(obj.leftItems));
            $.each(leftObj, function (index, item) {
                let checkItem = obj.AllItems.filter(function (x) { return x.value == item.value })
                if (checkItem.length == 0) {
                    obj.AllItems.push(item);
                }
            });
            obj.unselectedItems = JSON.parse(JSON.stringify(obj.leftItems));

            var rightObj = JSON.parse(JSON.stringify(obj.rightItems));
            $.each(rightObj, function (index, item) {
                let checkItem = obj.AllItems.filter(function (x) { return x.value == item.value })
                if (checkItem.length == 0) {
                    obj.AllItems.push(item);
                }
            });
            //obj.SetScrolbar('dark-thick', null, 'scrollmoverdiv');
        }, 1000);        
        setTimeout(function(){obj.SetBootstraptoolTip()}, 200);
        setTimeout(function(){obj.CheckBoxBootstrap()}, 100);
    }
}

</script>