<script>
import DataService from "../../../services/DataService";
export default {
  props: {
    submodulecode: {
      type: String,
      required: true,
    },
    modulename: {
      type: String,
      required: true,
    },
    callbackfunction: {
      type: Function,
    },
  },
  data: function () {
    return {
      filterData: [],
      showListData: true,
      showAddData: false,
      filterItemData: [],
      fieldNameData: [],
      options: null,
      submited: false,
      databaseCondition: "",
      condition: "",
      filterName: "",
      validationForName: "",
      filterId: 0,
      isDefault: false,
      FormatForSecond: "",
      isFirstTime: false,
      exitstingFilterNameArray: [],
      ApiURL: "",
      BaseUrl: "",
      ImageVal: "Image",
      popover: {
        visibility: 'click'
      },
      masks: {
      input: 'MM-DD-YYYY',
     },
    };
  },
  created: function () {
    this.GetSearchFilterData();
    this.GetSearchFilterItemData();
    this.GetFieldNameData();   
  },
  methods: {

    MakeArray: function (value, type) {
      var array = [];
      var arr = String(value).split(',');
      if (type == "radio" || type == "checkbox") {
        if (arr.length > 0) {
          for (var item of  arr) {

            if (item.split("|").length > 1) {
              var person = { name: item.split("|")[0], value: item.split("|")[1] };
              array.push(person);
            }
            else {
              var person = { name: item, value: item };
              array.push(person);
            }
          }
        }
      }
      else {
        if (arr.length > 0) {
          for (var item of arr) {
            var person = { name: item, value: item.trim() };
            array.push(person);
          }
        }
      }
      return array;
    },
    GetLocalizedValue: function (fieldName, displayName) {
      try {
        if (this.isBlank(displayName)) {
          var chk = this.$t(String(fieldName));
          if (chk != String(fieldName)) {
            return chk;
          }
          chk = this.$t(String(fieldName));
          if (chk || /^\s*$/.test(chk)) {
            return chk;
          }
          return fieldName;
        }
        else {
          var chk = this.$t(String(displayName));
          if (chk != String(displayName)) {
            return chk;
          }
          chk = this.$t(String(displayName));
          if (chk || /^\s*$/.test(chk)) {
            return chk;
          }
          return displayName;
        }
      }
      catch (ex) { console.log(ex) }
    },
    MakeNormalArray: function (value) {
      if (value) {
        try {
          return JSON.parse(value);
        }
        catch (ex) {
          return value;
        }
      }
      else {
        value = [];
      }
    },
    isBlank: function (str) {
      return !str || /^\s*$/.test(str);
    },
    returnDateObject: function (date) {
      var checkDate = null;
      date = date == undefined ? "" : date;
      if (date != "") {
        checkDate = new Date(Date.parse(date));
      }
      return {
        format: String(Globalize.culture().calendar.patterns.d).toUpperCase(),
        useStrict: true,
        useCurrent: false,
        showClear: true,
        showClose: true,
        allowInputToggle: true,
        minDate: checkDate,
        widgetPositioning: {
          horizontal: "right",
          vertical: "bottom",
        },
      };
    },
    GetValidationF: function (object) {
      var vm = this;

      if (
        vm.filterName != null &&
        vm.filterName != "" &&
        !vm.submited
      ) {
        this.$validator.reset();
        return object;
      }
      var obj = {};
      if (object.is_required) {
        obj["required"] = true;
      }
      if (object.length > 0) {
        if (object.dt_code == "textarea") {
          obj["max"] = 200;
        } else {
          obj["max"] = object.length;
        }
      }
      if (object.validation_type == "email") {
        obj["email"] = true;
      }
      if (object.dt_code == "date" || object.dt_code == "datetime") {
        obj["date_format"] = String(
          Globalize.culture().calendar.patterns.d
        ).toUpperCase();
      }
      if (object.dt_code == "decimal") {
        obj["decimal"] = 2;
      }
      if (object.dt_code == "int" || object.dt_code == "bigint") {
        obj["numeric"] = true;
      }
      if (object.dt_code == "url") {
        obj["url"] = "require_protocol";
      }
      if (
        object.regular_expression != null &&
        object.regular_expression != ""
      ) {
        obj["regex"] = object.regular_expression;
      }
      vm.submited=false;
      return obj;
    },
    onSearch:function onSearch(search, objItem) {
      this.searchSelectData(search, this, objItem);
    },
    searchSelectData:function (search, vm, objItem) {
      this.ShowSearchData(search, vm, objItem);
    },
    ShowSearchData: function (search, vm, objItem, selctedValue) {
      if (typeof (selctedValue) == 'undefined') {
                    selctedValue = 0;
                }
            var url = "id=&moduleName="+vm.modulename+"&type="+objItem.field_code;
            if (objItem.dependent_type == 'fill') {
                    url += "&refId=" + selctedValue;
                }
                DataService.getDDLData(url + "&search=" + escape(search))
                .then(response => {
                    var json = [];
                    if(response.data==''){
                       
                    }
                    if (response.data != '') {
                        json = response.data;
                    }                                   
                        return objItem.select_options = json.DATA;       
                });
   
    },
    setSelectedValue: function (item, val) {
      var vm = this;   
      try {
        setTimeout(() => {
          item.value = item.select_value;;
          vm.SetCondition();      
                }, 100);
       
       
      } catch (ex) { }
    },
    AddSearchData: function () {
      this.GetSearchFilterItemData();
    },
    DeleteSearchItem: function (item, index) {
      var vm = this;
      var msg = vm.$t("ConfirmDelete");
      vm.confirmR(msg, true, null,
        function () {
          vm.filterItemData.splice(index, 1);
          vm.SetCondition();
        });
    },
    GetSearchFilterData: function () {
      var vm = this;
      var param = "moduleName=" + vm.modulename + "&subModuleCode=" + vm.submodulecode;
      this.filterData = [];
      DataService.SearchCriteriaListing(param).then(function (response) {
        var json = response.data;
        if (json != null) {
         vm.filterData = json.DATA;
        }
      });
    },
    GetFieldNameData: function () {
      var vm = this;
      var param = "moduleName=" + vm.modulename + "&subModuleCode=" + vm.submodulecode;
      this.fieldNameData = [];
      DataService.GetFieldName(param).then(function (response) {
        var json = response.data;
        if (json != null) {
          // var chkData = $.grep(json.DATA, function (element, index) {
          //   return element.name != 'status_id';
          // });
          return (vm.fieldNameData = json.DATA.length > 0 ? json.DATA : undefined);
        }

      });
    },
    GetOperatorData: function ($event, item,index) {
      var vm = this;
      var fieldId = $event.target.value;
      // this.$refs.form.setFieldTouched('field_name'+(index+1), false);
      var chkData = $.grep(this.fieldNameData, function (element, index) {
        return element.custom_field_id == fieldId;
      });
      var datatype = "";
      item.dt_code = "";
      item.picklist_options = [];
      item.label = "";
      item.dropdown_type = "";
      item.select_options = "";
      item.select_value = "";
      item.length = 0;
      item.operator = "";
      item.field_code = "";
      item.second_value = "";
      item.display_name = "";
      item.is_required = true;
      if (chkData.length > 0) {
        datatype = chkData[0].sql_type;
        item.dt_code = chkData[0].dt_code;
        item.picklist_options = chkData[0].picklist_options;
        item.label = chkData[0].label;
        item.dropdown_type = chkData[0].dropdown_type;
        if (
          chkData[0].dropdown_type == "normal" &&
          chkData[0].picklist_options == "Lookup" &&
          chkData[0].name == "EVENT.event_reminder_id"
        ) {
          var reminder_option = vm.MakeNormalArray(chkData[0].select_options);
          $.each(reminder_option, function (ind, dropItem) {
            reminder_option[ind].name = vm.$t(dropItem.name);
          });
          item.select_options = reminder_option;
        } else {
          item.select_options = chkData[0].select_options;
        }
        item.length = chkData[0].length;
        item.display_name = chkData[0].display_name;
        item.field_code = chkData[0].field_code;
        item.is_required = chkData[0].is_required;
        item.validation_type = chkData[0].validation_type;
      }

      var url = "dataType=" + datatype + "&customField=" + fieldId;

      item.operator_list = [];
      DataService.GetOperator(url).then(function (response) {
        var json = response.data;

        if (json != null) {
          return (item.operator_list = json.DATA);
        }

      });
      item.field_name = fieldId;
      item.value = "";
      this.SetCondition();
    },
    SetValues: function ($event, item, type) {
      if (item.dt_code == "date" || item.dt_code == "decimal") {
        var regex = /^(\+|-)?(\d*\.?\d*)$/;
        if (!regex.test(item.value) && item.dt_code == "decimal") {
          item.value = "";
        }
        if (!regex.test(item.second_value) && item.dt_code == "decimal") {
          item.second_value = "";
        }
        if (type == "second") {
          var value = item.second_value?this.$options.filters.formatDate(item.second_value):item.second_value;
          item.second_value = value;
        } else {
          var value = item.value?this.$options.filters.formatDate(item.value):item.value;
          item.value = value;
          item.second_value = value;
        }
      } else {
        if (type == "second") {
          item.second_value = $event.target.value;
        } else {
          if($event.target.value)
          item.value = $event.target.value;
        }
      }
      this.SetCondition();
    },
    SetCondition: function () {
      var vm = this;      
      var conditionText = "";
      var databaseCondition = "";      
      var operatorExpression = "";
      $.each(vm.filterItemData, function (index, item) {
      
        if (index > 0) {
          conditionText += " And ";
          databaseCondition += " and ";
        }
        var fieldId = item.field_name;
        var chkData = $.grep(vm.fieldNameData, function (element, index) {
          return element.custom_field_id == fieldId;
        });

        if (typeof chkData != "undefined" && chkData.length > 0) {
          conditionText += vm.GetLocalizedValue(
            chkData[0].label,
            chkData[0].display_name
          );
          console.log(conditionText);
          if (chkData[0].dt_code == "date") {
            databaseCondition += "convert(date, " + chkData[0].name + ", 23)";
          } else {
            //databaseCondition += 'LTRIM(RTRIM(' + chkData[0].name.trimEnd() + '))';
            databaseCondition += "" + chkData[0].name.trimEnd() + "";
          }
        }
        var operatorId = item.operator;
        chkData = $.grep(item.operator_list, function (element, index) {
          return element.OPERATOR_ID == operatorId;
        });

        if (typeof chkData != "undefined" && chkData.length > 0) {
          conditionText =
            conditionText + " " + chkData[0].OPERATOR_DISPLAY_NAME;
          operatorExpression = chkData[0].OPERATOR_EXPRESSION;
          databaseCondition += " " + operatorExpression;
        }
        if (!vm.isBlank(item.value)) {
          var text = item.value;
          var value = item.value;
          if (
            item.dt_code == "select" &&
            item.dropdown_type == "normal" &&
            operatorExpression != "IN" &&
            operatorExpression != "NOT IN"
          ) {
            var array = vm.MakeNormalArray(item.select_options);
            chkData = $.grep(array, function (element, index) {
              return element.value == item.value;
            });
            if (typeof chkData != "undefined" && chkData.length > 0) {
              text = chkData[0].name;
            }
          } else if (
            (item.dt_code == "select" && item.dropdown_type == "custom") ||
            (item.dt_code == "select" &&
              item.dropdown_type == "normal" &&
              (operatorExpression == "IN" || operatorExpression == "NOT IN"))
          ) {
            //

            if (Array.isArray(item.value)) {
              value = vm.createString(item.value, "value");
              text = vm.createString(item.value, "name");
            } else {
              value = item.value.value?item.value.value:item.value;
              text = item.value.name;
            }
            if (operatorExpression == "IN" || operatorExpression == "NOT IN") {
              value = "(" + value + ")";
            }
          } else if (
            item.dt_code == "select" &&
            item.dropdown_type != "custom" &&
            item.dropdown_type != "normal"
          ) {
            if (operatorExpression == "IN" || operatorExpression == "NOT IN") {
              if (Array.isArray(item.value)) {
                value = vm.createString(item.value, "value");
                text = vm.createString(item.value, "name");
              } else {
                value = item.value.value;
                text = item.value.name;
              }
              value = "(" + value + ")";
            } else {
              var array = vm.MakeArray(item.picklist_options, item.dt_code);
              chkData = $.grep(array, function (element, index) {
                return element.value == item.value;
              });
              if (typeof chkData != "undefined" && chkData.length > 0) {
                text = chkData[0].name;
              }
              value = " '" + value + "'";
            }
          } else if (item.dt_code == "date") {
            if (operatorExpression == "between") {
              text = item.value + " And " + item.second_value;
              value =
                "'" + item.value +
                "'" +
                " and " +
                "'" +item.second_value +
                "'";
            } else {
              text = item.value;
              value = "'" + item.value + "'";
            }
          } else if (item.dt_code == "radio") {
            var array = vm.MakeArray(item.picklist_options, item.dt_code);
            chkData = $.grep(array, function (element, index) {
              return element.value == item.value;
            });
            if (typeof chkData != "undefined" && chkData.length > 0) {
              text = chkData[0].name;
              value = "'" + chkData[0].value + "'";
            }
          } else {
            if (
              operatorExpression == "Like" ||
              operatorExpression == "Not Like"
            ) {
              value = "'%" + item.value + "%'";
            } else if (operatorExpression == "Starts With") {
              value = "'" + item.value + "%'";
              databaseCondition = databaseCondition.replace(
                "Starts With",
                "like"
              );
            } else if (operatorExpression == "Ends With") {
              value = "'%" + item.value + "'";
              databaseCondition = databaseCondition.replace(
                "Ends With",
                "like"
              );
            } else if (operatorExpression == "between") {
              text = item.value + " And " + item.second_value;
              value =
                "'" +
                item.value +
                "'" +
                " and " +
                "'" +
                item.second_value +
                "'";
            } else if (
              operatorExpression == "IN" ||
              operatorExpression == "NOT IN"
            ) {
              
              var values = String(item.value).split(",");
              var str = "";
              for (var i = 0; i < values.length; i++) {
                if (i > 0) {
                  str += " , ";
                }
                str += "'" + values[i] + "'";
              }
              value = "(" + str + ")";
            } else {
              value = "'" + value + "'";
            }
          }
          if (
            item.dt_code == "select" &&
            item.dropdown_type == "normal" &&
            item.label == "event_location"
          ) {
            databaseCondition += " '" + value + "'";
          } else if (
            item.dt_code == "select" &&
            item.dropdown_type == "normal" &&
            item.label == "forecast_category_name"
          ) {
            databaseCondition +=
              operatorExpression.indexOf("IN") > -1
                ? " (" + text + ")"
                : " '" + text + "'";
          } else if (
            item.dt_code == "select" &&
            item.dropdown_type == "normal" &&
            item.label == "display_icon"
          ) {
            databaseCondition +=
              operatorExpression.indexOf("IN") > -1
                ? " " + value + ""
                : " '" + value + "'";
          } else if (
            item.dt_code == "select" &&
            item.dropdown_type == "normal" &&
            item.label == "contract_currency"
          ) {
            databaseCondition +=
              operatorExpression.indexOf("IN") > -1
                ? " " + value + ""
                : " '" + value + "'";
          } else {
            databaseCondition += " " + value;
          }
          conditionText = conditionText + " " + text;
        }
      });
      this.condition = conditionText;
      this.databaseCondition = databaseCondition;
     },
    ShowFieldValue: function ($event, item,index) {
      
      // this.$refs.form.setFieldValue('operator'+(index+1), item.operator);
      var id = $event.target.value;
      var chkData = $.grep(item.operator_list, function (element, index) {
        return element.OPERATOR_ID == id;
      });
      var expression = "";
      if (chkData.length > 0) {
        expression = chkData[0].OPERATOR_EXPRESSION;
      }
      if (expression == "between") {
        item.showSecondValue = true;
      } else {
        item.showSecondValue = false;
      }
      item.operator = id;
      item.value = "";
      item.select_value = [];
      item.operator_expression = expression;
      this.SetCondition();
    },
    GetSearchFilterItemData: function () {
      var obj = {
        field_name: "",
        dt_code: "",
        operator: "",
        operator_expression: "",
        operator_list: [],
        first_value: "",
        second_value: "",
        showSecondValue: false,
        picklist_options: [],
        label: "",
        display_name: "",
        dropdown_type: "",
        select_options: "",
        length: 0,
        field_code: "",
        select_value: "",
        is_required: true,
      };
      this.filterItemData.push(obj);
    },
    ApplyFilter: function (databaseCondition) {
    
      this.databaseCondition = databaseCondition;
      this.$parent.searchCondition = this.databaseCondition;
      if (
        typeof this.callbackfunction != "undefined" &&
        this.callbackfunction != null
      ) {
        this.$emit('FilterData', 1, 10, '', '', this.databaseCondition, this.filterItemData);
        this.$parent.appliedFilter = [];
        this.callbackfunction();
      } else {
        this.$parent.PageSize = 10;
        this.$parent.FetchData();
      }
      this.$emit("close");
    },
    EditFilter: function (item, type) {
      
      this.showListData = false;
      this.showAddData = true;
      if (type != "NotSaved") {
        this.filterId = item.FILTER_ID;
        this.EditSearchFilterData(this.filterId);
      } else {
        this.filterName =
          this.$parent.appliedFilter[0].field_name.split("(")[0];

        this.filterItemData = this.$parent.appliedFilter[0];
        this.SetCondition();
      }
    },
    deleteFilter: function (item) {
 this.filterId = item.FILTER_ID;
      var vm = this;


      vm.confirmR(vm.$t("ConfirmDeleteFilter"), true, null, function () {   
        var url = "id=" +
          item.FILTER_ID +
          "&moduleName=" +
          vm.modulename +
          "&subModuleCode=" +
          vm.submodulecode +
          "&isDelete=1";

        DataService.SearchCriteriaById(url)
          .then(
            function (response) {
              var json = response.data;
               var box = "";
              if (json != null && json.MSG == "SUCCESS") {
                vm.GetSearchFilterData();
                if (item.DATABASE_CONDITION == vm.$parent.searchCondition) {
                  vm.$parent.searchCondition = "";
                  if (
                    typeof vm.callbackfunction != "undefined" &&
                    vm.callbackfunction != null
                  ) {
                    vm.callbackfunction();
                  } else {
                    vm.$parent.FetchData();
                  }
                }
                vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Filter')]), 'success',true,vm.$t('Alert'));
              }
            }
          );
      });
    },
    checkEncodeURI(str) {
      
 return /\%/i.test(str)
},
    EditSearchFilterData: function (id) {
      var vm = this;
      var url = "id=" +
        id +
        "&moduleName=" +
        this.modulename +
        "&subModuleCode=" +
        this.submodulecode;
      vm.filterItemData = [];
      DataService.SearchCriteriaById(url)
        .then(
          function (response) {
            var json = response.data;
            if (json != null) {
             var fullJson = json.DATA;
              var chkData = $.grep(fullJson, function (element, index) {
                return element.FILTER_ID == id;
              });
             
             var isdbConditionEncoded= vm.checkEncodeURI(chkData[0].DATABASE_CONDITION);
             if(isdbConditionEncoded){
              vm.databaseCondition= decodeURIComponent(chkData[0].DATABASE_CONDITION);
             }else{
              vm.databaseCondition= chkData[0].DATABASE_CONDITION;
             }
              vm.filterName = chkData[0].FILTER_NAME;
              vm.condition = chkData[0].FILTER_CONDITION;
           
              vm.isDefault = chkData[0].IS_DEFAULT;
              $.each(fullJson, function (index, item) {
                
                var firstValue = item.FIRST_VALUE;
                var secondValue = item.SECOND_VALUE;
                if (item.DT_CODE == "date") {
                  //firstValue = vm.formatDate(firstValue);
                  if (item.OPERATOR_EXPRESSION == "between") {
                    //secondValue = vm.formatDate(secondValue);
                  }
                }
                var SelectedOptionsArray = [];
                if (
                  item.DT_CODE == "select" &&
                  (item.DROPDOWN_TYPE == "normal"|| (item.DROPDOWN_TYPE == null && item.PICKLIST_OPTIONS != "Lookup")) &&
                  (item.OPERATOR_EXPRESSION == "IN" || item.OPERATOR_EXPRESSION == " NOT IN")
                ) {
                  var valueArray = vm.MakeArray(
                    firstValue.replace(/'/g, ""),
                    item.dt_code
                  );

                  $.each(valueArray, function (index, selectedValue) {
                    if (
                      item.SELECT_OPTIONS.indexOf("[{") == -1 &&
                      item.SELECT_OPTIONS.length > 0
                    ) {
                      var SplitDat = item.SELECT_OPTIONS.split(",");
                      var newArray = [];
                      var selectedItm = [];
                      $.each(SplitDat, function (indx, dat) {
                        var nwdata = {
                          name: dat,
                          value: dat,
                        };
                        newArray.push(nwdata);
                      });
                      selectedItm = newArray.filter(function (chck) {
                        return chck.value == selectedValue.value;
                      });
                    } else {
                      selectedItm = vm
                        .MakeNormalArray(item.SELECT_OPTIONS)
                        .filter(function (chck) {
                          return chck.value == selectedValue.value;
                        });
                    }
                    if (selectedItm.length > 0) {
                      SelectedOptionsArray.push(selectedItm[0]);
                    }
                  });
                } else if (
                  item.PICKLIST_OPTIONS != "Lookup" &&
                  (item.OPERATOR_EXPRESSION == "IN" ||
                    item.OPERATOR_EXPRESSION == " NOT IN")
                ) {
                  SelectedOptionsArray = vm.MakeArray(
                    firstValue.replace(/'/g, ""),
                    item.dt_code
                  );
                } else {
                  SelectedOptionsArray = vm.MakeNormalArray(
                    item.SELECT_OPTIONS
                  );
                } 
                var obj = {
                  field_name: item.FIELD_ID,
                  dt_code: item.DT_CODE,
                  operator: item.OPERATOR_ID,
                  operator_expression: item.OPERATOR_EXPRESSION,
                  operator_list: vm.MakeNormalArray(item.OPERATOR_LIST),
                  value: firstValue,
                  second_value: secondValue,
                  showSecondValue: vm.isBlank(item.SECOND_VALUE) ? false : true,
                  picklist_options: item.PICKLIST_OPTIONS,
                  label: item.LABEL,
                  dropdown_type: item.DROPDOWN_TYPE,
                  select_options:
                    item.PICKLIST_OPTIONS != "Lookup" &&
                      (item.OPERATOR_EXPRESSION == "IN" ||
                        item.OPERATOR_EXPRESSION == " NOT IN")
                      ? item.SELECT_OPTIONS
                      : vm.MakeNormalArray(item.SELECT_OPTIONS),
                  length: item.LENGTH,
                  field_code: item.FIELD_CODE,
                  select_value: SelectedOptionsArray,
                  is_required: item.IS_REQUIRED,
                };
                vm.filterItemData.push(obj);
              });
            }
          }
        );
     
    },
    ClearFilter: function () {

      if (
        this.$parent.appliedFilter[0].searchCondition ==
        this.$parent.searchCondition
      ) {
        this.$parent.searchCondition = "";
        if (
          typeof this.callbackfunction != "undefined" &&
          this.callbackfunction != null
        ) {
          this.callbackfunction();
        } else {
          this.$parent.FetchData();
        }
      }
      this.$parent.appliedFilter = [];
    },
    SubmitData: function (type,values,meta) {
      
      var vm = this;
      vm.submited = true;
      // vm.$refs.form.setFieldTouched('name', true); 
      vm.$refs.form.validate().then((result) => { 
        console.log(this.$refs.form.errors);
       for(var err in result.errors){
        var node=document.getElementsByName(err);
        if(values[err] || node.length==0){
          delete result.errors[err];
        }else if(!values.hasOwnProperty(err)){          
          delete result.errors[err];
        }
       }       
      //  result= Object.keys(result.errors).length === 0?true:result;
        if (result) {
          if (type == "search") {
            vm.$parent.searchcondition = vm.databaseCondition;
            if (vm.filterName == "") {
              vm.filterItemData.field_name = "New Created Filter(Not Saved)";
            } else {
              vm.filterItemData.field_name = vm.filterName + "(Not Saved)";
            }
            vm.filterItemData.searchCondition = vm.databaseCondition;
            vm.$parent.appliedFilter = [];
            vm.$parent.PageNumber = 1;
            vm.$parent.PageSize = 10;
            vm.$parent.SortBy = "";
            vm.$parent.SortExp = "";
            if (
              typeof vm.callbackfunction != "undefined" &&
              vm.callbackfunction != null
            ) {
              vm.$emit('FilterData', 1, 10, '', '', vm.databaseCondition, vm.filterItemData);
            } else {
              vm.$parent.FetchData();
            }
            vm.$emit("close");
          } else {
            var checkDuplicacy = [];
            if (vm.filterId == 0) {
              checkDuplicacy = $.grep(vm.filterData, function (n, i) {
                return n.FILTER_NAME == $.trim(vm.filterName);
              });
            }
            if (checkDuplicacy.length == 0) {
              vm.$parent.appliedFilter = [];
              var fullJson = vm.filterItemData;
              var data = [];
              $.each(fullJson, function (Key, item) {
                var value = item.value;
                var secondValue = item.second_value;
                if (
                  (item.dt_code == "select" &&
                    item.dropdown_type == "custom") ||
                  (item.dt_code == "select" &&
                    item.picklist_options != "Lookup" &&
                    (item.operator_expression == "IN" ||
                      item.operator_expression == "NOT IN")) ||
                  (item.dt_code == "select" &&
                    item.dropdown_type == "normal" &&
                    (item.operator_expression == "IN" ||
                      item.operator_expression == "NOT IN"))
                ) {
                  if (Array.isArray(item.value)) {
                    value = vm.createString(item.value, "value");
                  } else {
                    value = item.value.value?item.value.value:item.value;
                  }
                }
                if (item.dt_code == "date") {
                  if (
                    !vm.isBlank(secondValue) &&
                    item.operator_expression == "between"
                  ) {
                    //secondValue = vm.$options.filters.formatDate(secondValue);
                  }
                  //value = vm.$options.filters.formatDate(item.value);
                }
                var obj = {
                  fieldId: item.field_name,
                  operatorId: item.operator,
                  firstValue: value,
                  secondValue: secondValue,
                };
                data.push(obj);
              });
              var postJSON = {
                filterId: vm.filterId,
                filterName: vm.filterName,
                condition: vm.condition,
                databaseCondition: vm.databaseCondition,
                isDefault: vm.isDefault,
                moduleName: vm.modulename,
                subModuleCode: vm.submodulecode,
                data: data,
              };
              var objectPostString = JSON.stringify(postJSON);

              DataService.SaveFilter(objectPostString).then(
                function (response) {
                 vm.$parent.PageNumber = 1;
                  vm.$parent.PageSize = 10;
                  vm.$parent.SortBy = "";
                  vm.$parent.SortExp = "";
                  vm.$parent.searchCondition = vm.databaseCondition;
                  if (
                    typeof vm.callbackfunction != "undefined" &&
                    vm.callbackfunction != null
                  ) {
                 
                    vm.$emit('FilterData', 1, 10, '', '', vm.databaseCondition, null);
                    vm.$parent.appliedFilter = [];
                  } else {
                    vm.$parent.FetchData();
                  }
                  vm.$emit("close");

                });
            } else {
              this.ShowAlert(this.$t("FilterNameAlreadyExist"));
            }
          }
        }
      });
    },
  }
};
</script>
<template>
  <transition name="modal">
    <div class="my-popups">
      <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
          <div class="modal-content">
            <div class="modal-header">
              <h4 name="header" class="modal-title">{{$t("Search")}}</h4>
              <button v-on:click="$emit('close')" href="javascript:;" class="close">
                <em class="fa fa-times" aria-hidden="true"></em>
              </button>
            </div>
            <div class="modal-body">
              <div class="row">
                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
                  <div class="form-content">
                    <div id="divshowSearchCriteriaList" class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left"
                      v-if="showListData">
                      <div class="form-group is-invalid" v-if="filterData.length>=10">
                        <div role="alert" class="invalid-feedback alert alert-danger" style="display: block;">
                          {{$t("NotAddMoreThanTen")}}
                          <a href="javascript:;">
                            <em class="fa fa-close float-right text-danger"></em>
                          </a>
                        </div>
                      </div>
                      <div class="heading-border">
                        <h5 class="h5">
                          <span> {{$t("Filter") }}</span>
                          <small class="float-right" v-if="filterData.length<10">
                            <a href="javascript:;"
                              v-on:click="showListData=false;showAddData=true;filterName='';filterId=0;conditionText=''"
                              :title="$t('Add')">
                              <em class="fa fa-plus pr-2"></em>
                              {{$t("Add")}}
                            </a>
                          </small>
                        </h5>
                      </div>
                      <div class="listing mt-2">
                        <div class="table-responsive">
                          <table class="table table-bordered dt-responsive nowrap mt-0 dataTable no-footer dtr-inline" aria-describedby="serachfilter">
                            <thead class="thead-bg">
                              <tr>
                                 <th scope="col" style="min-width:480px">
                                  <span>{{$t('Name')}}</span>
                                </th>

                                 <th scope="col" class="text-center" style="min-width:70px">
                                  <span>{{$t('Edit')}}</span>
                                </th>
                                 <th scope="col" class="text-center" style="min-width:70px">
                                  <span>{{$t('Apply')}}</span>
                                </th>
                                 <th scope="col" class="text-center" style="min-width:70px">
                                  <span>{{$t('Delete')}}</span>
                                </th>
                              </tr>
                            </thead>
                            <tbody>
                              <tr v-for="(item, itemindex) in $parent.appliedFilter" :key="itemindex">
                                <td>{{item.field_name}}</td>
                                <td class="text-center single-action">
                                  <a href="javascript:;" :title=" $t('Edit')" class="pr-2"
                                    v-on:click="EditFilter(item, 'NotSaved')">
                                    <em class="fa fa-pencil text-success action-icon" aria-hidden="true"></em>
                                  </a>
                                </td>
                                <td class="text-center single-action">
                                  <a href="javascript:;" :title=" $t('Apply')"
                                    v-on:click="ApplyFilter(item.searchCondition)" class="pr-2">
                                    <em v-bind:class="{'fa fa-check action-icon':true, 'text-success': $parent.searchCondition == item.searchCondition,
                                    'text-primary':$parent.searchCondition != item.searchCondition}"
                                      aria-hidden="true"></em>
                                  </a>
                                </td>
                                <td class="text-center single-action">
                                  <a href="javascript:;" :title=" $t('Delete')" v-on:click="ClearFilter()">
                                    <em class="fa fa-trash text-danger action-icon" aria-hidden="true"></em>
                                  </a>
                                </td>
                              </tr>
                            
                              <tr v-for="(item, index1) in filterData" v-bind:key="index1">
                                <td>{{item.FILTER_NAME}}</td>
                                <td class="text-center single-action">
                                  <a href="javascript:;" :title="$t('Edit')" class="pr-2"
                                    v-on:click="EditFilter(item, '')">
                                    <em class="fa fa-pencil text-success action-icon" aria-hidden="true"></em>
                                  </a>
                                </td>
                                <td class="text-center single-action">
                                  <a href="javascript:;" :title=" $t('Apply')"
                                    v-on:click="ApplyFilter(item.DATABASE_CONDITION)" class="pr-2">
                                    <em v-bind:class="{'fa fa-check action-icon':true, 'text-success': $parent.searchCondition == item.DATABASE_CONDITION, 'text-primary':$parent.searchCondition != item.DATABASE_CONDITION}"
                                      aria-hidden="true"></em>
                                  </a>
                                </td>
                                <td class="text-center single-action">
                                  <a href="javascript:;" :title=" $t('Delete')" v-on:click="deleteFilter(item)">
                                    <em class="fa fa-trash text-danger action-icon" aria-hidden="true"></em>
                                  </a>
                                </td>
                              </tr>
                              <tr
                                v-if="(filterData == null || filterData.length==0) && ($parent.appliedFilter==undefined || $parent.appliedFilter==null || 
                                ($parent.appliedFilter!=undefined && $parent.appliedFilter.length == 0))">
                                <td colspan="3" class="text-center text-danger py-3">{{$t('NoRecordfound')}}</td>
                              </tr>
                            </tbody>
                          </table>
                       
                        </div>
                      </div>
                    </div>
                    <div id="divManageSearchCriteria" class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left"
                      v-if="showAddData">
                      <Form ref="form" v-slot="{ values,meta}">
                      <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left">
                       
                          <div class="col-md-12">
                            <div class="row">
                              <div class="col-md-4 px-0">
                                <div class="form-group mb-0">
                                  <label>
                                    {{$t('Name')}}:
                                    <span class="text-danger">*</span>
                                  </label>
                                  <Field name="name" :rules="{'required':true}" v-slot="{errors}">
                                    <input v-model.trim="filterName" :class="{'form-control':true,'is-invalid':errors[0]}" name="name"  maxlength="100" type="text" />
                                    <span v-if="errors[0]" name="name" class="invalid-feedback">{{errors[0]}}</span>
                                  </Field>
                                </div>
                              </div>
                              <div class="listing mt-2 below768 p-0">
                                <div class="table-responsive overflow-visible">
                                  <table aria-describedby="search-filter-table"
                                    class="table table-bordered dt-responsive nowrap mt-0 dataTable no-footer dtr-inline">
                                    <thead class="thead-bg">
                                      <tr>
                                         <th scope="col" style="min-width:110px;max-width:110px">
                                          <span>{{$t('fieldName')}}</span>
                                        </th>
                                         <th scope="col" style="min-width:160px;max-width:110px">
                                          <span>{{$t('ConditionalOperator')}}</span>
                                        </th>
                                         <th scope="col" style="min-width:350px;">
                                          <span>{{$t('Value')}}</span>
                                        </th>
                                         <th scope="col" class="text-center">
                                          <a href="javascript:void(0)" v-on:click="AddSearchData" class="text-success">
                                            <em class="fa fa-plus pr-2"></em>
                                            {{$t('Add')}}
                                          </a>
                                        </th>
                                      </tr>
                                    </thead>
                                    <tbody>
                                      <tr v-for="(item, index) in filterItemData" v-bind:key="index">
                                        <td>
                                          <Field :name="'field_name'+(index+1)" :rules="{'required':true}" label="Field Name"                                          
                                           v-slot="{errors}">
                                            <select   v-model="item.field_name" v-on:change="GetOperatorData($event,item,index)"
                                             :name="'field_name'+(index+1)" :class="{'form-control disableAria' : true,'is-invalid':errors[0]}"
                                               >
                                              <option  value>{{$t("Select")}}</option>
                                              <option v-for="(option,index3) in fieldNameData"
                                              v-bind:value="String(option.custom_field_id)" :key="index3">
                                              {{GetFieldDisplayName(option.label,option.display_name)}}</option> 
                                            </select> 
                                          </Field>                                         
                                        </td>
                                        <td>
                                          <Field :name="'operator'+(index+1)" :rules="{'required':true}" label="operator"                                           
                                            v-slot="{errors}">
                                            <select v-model="item.operator" :name="'operator'+(index+1)" :class="{'form-control' : true,'disableAria':true,'is-invalid':errors[0]}"
                                              v-on:change="ShowFieldValue($event,item,index)">
                                              <option value>{{$t("Select")}}</option>
                                              <option v-for="(option,index2) in item.operator_list"
                                              v-bind:value="String(option.OPERATOR_ID)" :key="index2">
                                              {{option.OPERATOR_DISPLAY_NAME}}</option>
                                            </select>                                           
                                            <!-- <span v-if="errors[0]"  :name="'operator'+(index+1)"
                                              class="invalid-feedback d-block">{{errors[0]}}</span> -->
                                          </Field>  
                                        </td>
                                        <td class="overflow-visible">                                           
                                              <div
                                                v-bind:class="{'col-5 p-0 float-left pr-1' : item.operator_expression=='between'}"
                                                v-if="item.dt_code=='date' ||  item.dt_code=='datetime'">
                                                <Field :name="'date'+item.label+index"
                                                  :rules="{'required':item.is_required}" :label="item.label+index"
                                                  v-slot="{errors}">
                                                  <v-date-picker :popover="popover"  :minute-increment="5" :masks="masks"
                                                  class="input-group" v-model="item.value"  v-bind:name="'date'+item.label+index"
                                                  @dayclick="SetValues($event,item)"
                                                    v-bind:class="{'disableAria' : true,'is-invalid': (item.dt_code=='date' || item.dt_code=='datetime') && errors[0]}"
                                                    v-if="item.dt_code=='date' || item.dt_code=='datetime'"  v-bind:value="$options.filters.formatDate(item.value)">
                                                    <template v-slot="{ inputValue, inputEvents ,togglePopover }">
                                                      <input class="form-control"  :value="inputValue" v-on="inputEvents" />
                                                      <div @click="togglePopover()" class="input-group-append"><span class="input-group-text"><em class="fa fa-calendar"></em></span></div>
                                                    </template>
                                                  </v-date-picker>
                                                </Field>
                                              </div>
                                              <div style="margin-top: 6px;" class="col-2 float-left"
                                                v-if="(item.dt_code=='date' || item.dt_code=='datetime') && item.operator_expression == 'between'">
                                                {{$t('And','And')}}</div>
                                              <div
                                                v-bind:class="{'input-group': true , 'col-5 p-0 float-left' : item.operator_expression=='between'}"
                                                v-if="(item.dt_code=='date' || item.dt_code=='datetime') && item.operator_expression=='between'">
                                                <Field :name="'second'+item.label+index"
                                                  :rules="{'required':item.is_required}" :label="item.label+index"
                                                  v-slot="{errors}">
                                                  <v-date-picker :popover="popover" :minute-increment="5" 
                                                  class="input-group" v-model="item.second_value" :masks="masks"
                                                    v-bind:name="'second'+item.label+index" ref="SecondDatePicker"
                                                    @dayclick="SetValues($event,item,'second')"
                                                    v-bind:class="{'disableAria' : true,'is-invalid':(item.dt_code=='date' || item.dt_code=='datetime') && errors[0] }"
                                                    v-if="item.dt_code=='date' || item.dt_code=='datetime'">
                                                    <template v-slot="{ inputValue, inputEvents ,togglePopover }">
                                                      <input class="form-control"  :value="inputValue" v-on="inputEvents" />
                                                      <div @click="togglePopover()" class="input-group-append"><span class="input-group-text"><em class="fa fa-calendar"></em></span></div>
                                                    </template>
                                                  </v-date-picker>
                                                </Field>
                                              </div>
                                              <Field  v-if="item.picklist_options != 'Lookup' && item.dt_code !='date' && item.dt_code!='datetime' && item.dt_code !='radio'  
                                                  && item.dt_code !='select' && item.dt_code !='textarea' && item.dt_code !='checkbox'"
                                              :name="item.label+index" :rules="{'required':item.is_required}"
                                                label="Value" v-slot="{errors}">
                                                <input v-bind:id="item.label+index" v-bind:name="item.label+index"                                                 
                                                  v-bind:class="{'form-control disableAria': true,'is-invalid': errors[0],'col-5 float-left' : item.operator_expression=='between'  }"
                                                  v-on:input="SetValues($event,item)" type="text"  v-model="item.value"
                                                  />
                                              </Field>
                                              <div style="margin-top: 6px;" class="col-2 float-left"
                                                v-if="item.picklist_options != 'Lookup' && item.dt_code !='date'  
                                                  && item.dt_code!='datetime' && item.dt_code !='radio'  && item.dt_code !='select' && item.dt_code !='textarea' &&
                                                item.dt_code !='checkbox' && item.operator_expression == 'between'">{{$t('And','And')}}
                                              </div>
                                              <Field  v-if="item.picklist_options != 'Lookup' && item.dt_code !='date'   &&
                                                  item.dt_code!='datetime' && item.dt_code !='radio'  && item.dt_code !='select' && item.dt_code !='textarea' && 
                                                  item.dt_code !='checkbox' && item.operator_expression == 'between'"
                                              :name="'second'+item.label+index"
                                                :rules="{'required':item.is_required}"
                                                :label="'second'+item.label+index" v-slot="{errors}">
                                                <input 
                                                  v-bind:class="{'form-control disableAria' : true,'is-invalid': errors[0],'col-5 float-left' : item.operator_expression=='between'}"
                                                  v-on:input="SetValues($event,item,'second')" type="text"
                                                  v-model="item.second_value" v-bind:id="item.label"
                                                  v-bind:name="'second'+item.label+index"
                                                  />
                                              </Field>
                                              <Field :name="'textarea'+item.label+index" v-if="item.dt_code=='textarea'"
                                                :rules="{'required':item.is_required}" :label="item.label+index"
                                                v-slot="{errors}">
                                                <textarea
                                                  v-bind:class="{'form-control' : true,'is-invalid': item.dt_code == 'textarea' && errors[0]}"
                                                  v-model="item.value" v-bind:id="item.label"
                                                  v-on:change="SetValues($event,item)"
                                                  ></textarea>
                                              </Field>
                                              <div class="form-control pl-4 border-0 bg-transparent"
                                                v-if="item.dt_code=='radio'">
                                                <div class="form-check form-check-inline">
                                                  <div
                                                    class="form-check form-check-inline custom-control mx-4 pl-2 custom-radio"
                                                    v-for="(option,optionIndex) in MakeArray(item.picklist_options,item.dt_code)"
                                                    v-bind:key="option.name" :key="optionIndex">
                                                    <Field :name="'rdo_'+item.custom_field_id+option.value" :rules="{'required':item.is_required}"
                                                      :label="item.label">
                                                      <input
                                                        v-bind:id="'rdo_'+item.custom_field_id+option.value"
                                                        class="dynamic form-check-input custom-control-input"
                                                        v-model="item.value" v-bind:name="'rdo_'+item.custom_field_id+option.value"
                                                        v-on:change="SetValues($event,item)"
                                                        v-bind:data-resource="option.name" v-bind:value="option.value"
                                                        type="radio" v-bind:checked="option.value == item.value" />
                                                    </Field>
                                                    <label
                                                      class="custom-control-label universalradio-custom-control-label pl-0 pt-0 dynamic"
                                                      v-bind:for="'rdo_'+item.custom_field_id+option.value">{{option.name}}</label>
                                                  </div>
                                                </div>
                                              </div>
                                              <div class="form-control pl-0 border-0 bg-transparent"
                                                v-if="item.dt_code=='checkbox'">
                                                <div class="form-check form-check-inline">
                                                  <div class="custom-control custom-checkbox"
                                                    v-for="(option,optionIndex1) in MakeArray(item.picklist_options,item.dt_code)"
                                                    :key="optionIndex1">
                                                    <Field :name="'chk_'+item.custom_field_id+option.value" :rules="{'required':item.is_required}"
                                                      :label="item.label">
                                                      <input 
                                                        v-bind:id="'chk_'+item.custom_field_id+option.value"
                                                        class="dynamic custom-control-input" v-model="item.select_value"
                                                        v-bind:name="'chk_'+item.custom_field_id+option.value" v-bind:data-resource-id="option.name"
                                                        v-bind:value="option.value" type="checkbox"
                                                        v-on:change="SetValues($event,item)" />
                                                    </Field>
                                                    <label
                                                      class="custom-control-label universal-custom-control-label pl-2 pr-1 dynamic"
                                                      v-bind:for="'chk_'+item.custom_field_id+option.value">{{option.name}}</label>
                                                  </div>
                                                </div>
                                              </div>
                                              <Field v-if="item.picklist_options == 'Lookup' && item.dt_code=='select' && (item.dropdown_type=='custom' || 
                                                  (item.dropdown_type=='normal' && (item.operator_expression=='IN' || item.operator_expression=='NOT IN')))"
                                              :name="'Multiselect'+item.label+index"
                                                :rules="{'required':item.is_required}" :label="item.label+index"
                                                v-slot="{errors}">
                                                <Multiselect  :multiple="(item.operator_expression=='IN' || item.operator_expression=='NOT IN')?true:false"  
                                                :name="'Multiselect'+item.label+index"
                                                  v-bind:class="{'disableAria':true,'is-invalid': item.picklist_options == 'Lookup' && 
                                                  item.dt_code=='select' && item.dropdown_type=='custom' && errors[0]}"
                                                   placeholder="Search a value" v-model="item.select_value" v-bind:value="item.value" 
                                                  :filter-results="false"  :searchable="true"  track-by="value" 
                                                  :show-no-results="false" :internal-search="false" :clear-on-select="false" :close-on-select="true"
                                                  :options="(item.dropdown_type=='normal')?MakeNormalArray(item.select_options):item.select_options" :object="true" label="name"                                     
                                                  @search-change="onSearch($event,item)"
                                                  :showNoOptions="true"
                                                  @select="setSelectedValue(item,$event)">
                                                  <span slot="noOptions">
                                               Type to search {{ $t(item.label,item.display_name) }}
                                                </span> </Multiselect>                                     
                                              </Field>
                                              <Field  v-if="item.picklist_options == 'Lookup' && item.dt_code=='select' && item.dropdown_type=='normal' && 
                                                (item.operator_expression != 'IN' &&  item.operator_expression != 'NOT IN')"
                                              :name="'select'+item.label+index" :rules="{'required':item.is_required}"    
                                              v-model="item.value" v-slot="{errors}">
                                            <select  :name="'select'+item.label+index" v-on:change="SetValues($event,item,index)"
                                            v-bind:class="{'form-control disableAria' : true,'is-invalid':errors[0]}">
                                              <option value>{{$t("Select")}}</option>
                                              <option v-for="(option,optionKey) in MakeNormalArray(item.select_options)" :key="optionKey"                                              
                                                  v-bind:value="option.value">{{option.name}}</option>
                                            </select>
                                          </Field>    
                                          <Field  v-if="item.picklist_options != 'Lookup' && item.dt_code=='select' && 
                                                (item.operator_expression != 'IN' &&  item.operator_expression != 'NOT IN')"
                                          :name="'select2'+item.label+index" :rules="{'required':true}"   v-model="item.value"                                             
                                           v-slot="{errors}">
                                            <select  :name="'select2'+item.label+index"
                                            v-bind:class="{'form-control disableAria' : true,'is-invalid':errors[0]}"
                                            v-on:change="SetValues($event,item,index)">
                                              <option value>{{$t("Select")}}</option>
                                              <option v-for="(option) in MakeArray(item.picklist_options,item.dt_code)" v-if="item.picklist_options!=null"
                                                  v-bind:selected="item.value==option.value"
                                                  v-bind:value="option.value">{{option.name}}</option>
                                            </select>
                                          </Field> 
                                              <Field  v-if="item.picklist_options != 'Lookup' && item.picklist_options != null && item.dt_code=='select' &&(item.operator_expression=='IN' || item.operator_expression=='NOT IN')" 
                                              :name="'Multiselect2'+item.label+index"
                                                :rules="{'required':item.is_required}" label="name"
                                                v-slot="{errors}">
                                                <Multiselect
                                                v-bind:multiple="(item.operator_expression =='IN' || item.operator_expression=='NOT IN')?true:false"
                                                 v-bind:taggable="(item.operator_expression =='IN' || item.operator_expression=='NOT IN')?true:false"
                                                  v-bind:class="{'disableAria':true,'is-invalid': item.picklist_options == 'Lookup' && 
                                                  item.dt_code=='select' && item.dropdown_type=='custom' && errors[0]}"
                                                  v-model="item.select_value"  placeholder="Search a value"  :showNoOptions="true"
                                                  :filter-results="false"  :searchable="true"  track-by="value"
                                                  :show-no-results="false" :internal-search="false" :clear-on-select="false" :close-on-select="true"
                                                  :options="MakeArray(item.picklist_options,item.dt_code)" :object="true" label="name"
                                                  @search-change="onSearch($event,item)"  @select="setSelectedValue(item,$event)">
                                                  <span slot="noOptions">
                                                 Type to search {{ $t(item.label,item.display_name) }}
                                                </span>
                                                </Multiselect>                             
                                              </Field>
                                        </td>
                                        <td class="text-center single-action">
                                          <a v-if="index!=0" href="javascript:;"
                                            v-on:click="DeleteSearchItem(item,index)">
                                            <em class="fa fa-trash action-icon text-danger"></em>
                                          </a>
                                        </td>
                                      </tr>
                                    </tbody>
                                  </table>
                                
                                </div>
                              </div>
                              <div class="col-12 float-left p-0 mt-2" v-if="!isBlank(condition)">
                                <div class="alert alert-secondary py-2">
                                  <label class="font-weight-bold mb-0">{{$t("Condition")}}:</label>
                                  <label class="mb-0">{{condition}}</label>
                                </div>
                              </div>
                              <div class="col-12 float-left p-0 mt-2 d-none">
                                <div class="alert alert-secondary py-2">
                                  <label class="font-weight-bold mb-0">
                                    Database
                                    Condition:
                                  </label>
                                  <label class="mb-0">{{databaseCondition}}</label>
                                </div>
                              </div>
                            </div>
                          </div>                       
                      </div>
                      <div class="modal-footer text-right pr-0 pb-0">
                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0">
                          <a href="javascript:;" :class="{'btn btn-success mr-2':true}" v-on:click="SubmitData('save', values,meta)"
                            :title="$t('SaveAndSearch')">
                            <em class="fa fa-floppy-o pr-2" aria-hidden="true"></em>
                            {{$t("SaveAndSearch") }}
                          </a>
                          <a href="javascript:;" class="btn btn-success mr-2" v-on:click="SubmitData('search', values,meta)"
                            :title="$t('Search')">
                            <em class="fa fa-search pr-2" aria-hidden="true"></em>
                            {{$t("Search") }}
                          </a>
                          <a href="javascript:;" class="btn btn-primary delete-btn"
                            v-on:click="showListData=true;showAddData=false;filterItemData = [];GetSearchFilterItemData();"
                            :title="$t('Cancel')">
                            <em class="fa fa-times pr-2" aria-hidden="true"></em>
                            {{$t("Cancel") }}
                          </a>
                        </div>
                      </div>
                    </Form>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>