<template>
    <section>
        <div class="w-100 float-left">

            <div class="w-100 float-left">
                <div class="heading-border">
                    <h5 class="h5">
                        <span>Start Your Campaign With:</span>
                    </h5>
                </div>
            </div>

            <div class="listing col-12 p-0 mt-3">
                <div class="row custom-box-width default_templates">

                    <a class="col-sm-12 col-md-6 col-lg-4 col-xl-3 default-tag-vissible" href="javascript:;"
                        v-on:click="loginByFacebook()">
                        <div class="box positionrelative float-left w-100">
                            <div class="img-ico text-center p-2 ticket-attachments_small">
                                <em class="fa fa-file-text-o font-pic-size-28 mr-3"></em> <span
                                    class="font-weight-medium">Facebook</span>
                            </div>
                        </div>
                    </a>

                    <a class="col-sm-12 col-md-6 col-lg-4 col-xl-3 default-tag-vissible" href="javascript:;"
                        v-on:click="loginByInstagram()">
                        <div class="box positionrelative float-left w-100">
                            <div class="img-ico text-center p-2 ticket-attachments_small">
                                <em class="fa fa-file-text-o font-pic-size-28 mr-3"></em> <span
                                    class="font-weight-medium">Instagram</span>
                            </div>
                        </div>
                    </a>

                    <a class="col-sm-12 col-md-6 col-lg-4 col-xl-3 default-tag-vissible" href="javascript:;">
                        <div class="box positionrelative float-left w-100">
                            <div class="img-ico text-center p-2 ticket-attachments_small">
                                <em class="fa fa-file-text-o font-pic-size-28 mr-3"></em> <span
                                    class="font-weight-medium">Google</span>
                            </div>
                        </div>
                    </a>

                    <a class="col-sm-12 col-md-6 col-lg-4 col-xl-3 default-tag-vissible" href="javascript:;">
                        <div class="box positionrelative float-left w-100">
                            <div class="img-ico text-center p-2 ticket-attachments_small">
                                <em class="fa fa-file-text-o font-pic-size-28 mr-3"></em> <span
                                    class="font-weight-medium">Bing</span>
                            </div>
                        </div>
                    </a>
                </div>
            </div>
            <div class="row" v-if="LoginStatus=='Facebook'">
                <div class="col-lg-12 mt-2">
                    <div class="heading-border mb-2">
                        <h5 class="h5">
                            <span>Connected Account & Pages</span>
                            <small class="float-right mt-2">
                                <a id="addNewPost" v-on:click="AddNewPost()" title="Add New Post">
                                    <em class="fa fa-plus pr-2"></em> Add New Post
                                </a>
                            </small>
                        </h5>
                    </div>
                </div>
            </div>
            <div v-if="LoginStatus=='Facebook'">
            <tg-list :IsShowAction="  true " ModuleName="CRM" SubModuleCode="CRM_CAMPAIGN"
                :RenderRowActionMethod=" renderActions " v-on:ActionButtonClick=" actionButtonClick "
                IdentityColumn="REF_PAGE_ID" :HeaderText=" $t('Campaign') " :listType=" ['List'] " :ListData=" AllPages "
                :HeaderData=" Headers " :TotalRecords=" TotalRecords " :ListDataCallBackFunction=" GetPageList "
                :showCheckBox=" false " :SortExp=" SortExp " :SortBy=" SortBy " :NorecordfoundText=" $t('NoRecordfound') "
                v-on:LeftsearchButtonClick=" leftCommonSearch " v-on:PagerButtonClick=" pagerMethod "
                v-on:SortdataButtonClick=" sortdata ">
                <template v-slot:slotdata="{ data }">
                    <template v-if=" data.column.COLUMN_NAME == 'CAMPAIGN_TYPE_NAME' ">
                        <a href="javascript:;" v-on:click="loginByFacebook()" :title=" data.row[data.column.COLUMN_NAME] ">
                            {{ data.row[data.column.COLUMN_NAME] }}
                        </a>
                    </template>
                </template>
            </tg-list>
        </div>
        </div>
    </section>
</template>
<script>
import DataService from '../../../services/DataService';
export default {
    data() {
        return {
            FacebookAppId: "373917678908589",
            FacebookConfigurationId: "392108006804595",
            FacebookAuthCallBackUrl: "https://3635-202-164-57-195.ngrok-free.app/CRM/SMCampaignListing",
            AccountPageData: [],
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            SortBy: '',
            SortExp: '',
            AllPages: [],
            CampaignType: 'FACEBOOK',
            LoginStatus:'',
            status: '',
            Headers: [
                {
                    "COLUMN_NAME": "CAMPAIGN_TYPE_NAME",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Account",
                    "DISPLAY_ORDER": 1
                },
                {
                    "COLUMN_NAME": "REF_PAGE_TITLE",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Page Name",
                    "DISPLAY_ORDER": 2
                }
            ],
            listActions:
                [
                    {
                        title: 'Delete',
                        href: 'javascript:void(0)',
                        icon: 'fa fa-trash',
                        callbackfunction: 'DeleteCampaign',
                        additionalSpan: ''
                    }
                ]
        }
    },
    async created() {
        var vm = this;
        await vm.GetPageList();
        vm.getLoginData();
    },
    // mounted() {
    //     window.fbAsyncInit = function() {
    //         FB.init({
    //         appId: '335046346019611',
    //         autoLogAppEvents: true,
    //         xfbml: true,
    //         version: 'v19.0', // Use the latest version
    //         });
    // };

    // // Load the SDK asynchronously
    // (function(d, s, id) {
    //     var js, fjs = d.getElementsByTagName(s)[0];
    //     if (d.getElementById(id)) return;
    //     js = d.createElement(s); js.id = id;
    //     js.src = "https://connect.facebook.net/en_US/sdk.js";
    //     fjs.parentNode.insertBefore(js, fjs);
    // }(document, 'script', 'facebook-jssdk'));
    // },
    methods: {
        loginByFacebook() {
            var vm = this;
            window.location.href = "https://graph.facebook.com/oauth/authorize?client_id=" + encodeURIComponent(vm.FacebookAppId.toString()) +
                "&config_id=" + encodeURIComponent(vm.FacebookConfigurationId.toString()) +
                "&redirect_uri=" + encodeURIComponent(vm.FacebookAuthCallBackUrl.toString()) +
                "&scope=manage_pages,publish_pages";
        },
        getLoginData() {
            var vm=this;
            const data = localStorage.getItem('facebook_login_data');
            if (data) {
               vm.LoginStatus='Facebook';
            }else{
                vm.LoginStatus='';
            }
            return null; 
        },
        // async loginByFacebook() {
        //     debugger;
        //     try {
        //         const vm = this;
        //         const url = `https://graph.facebook.com/oauth/authorize?client_id=${encodeURIComponent(vm.FacebookAppId.toString())}&config_id=${encodeURIComponent(vm.FacebookConfigurationId.toString())}&redirect_uri=${encodeURIComponent(vm.FacebookAuthCallBackUrl.toString())}&scope=manage_pages,publish_pages`;
        //         const response = await fetch(url);

        //         // Check if the response was successful
        //         if (!response.ok) {
        //             throw new Error(`Failed to fetch: ${response.status} ${response.statusText}`);
        //         }

        //         const data = await response.json();
        //         console.log('Facebook API response:', data);
        //         // Now you can use the data retrieved from Facebook API in your application
        //     } catch (error) {
        //         console.error('Error fetching data from Facebook API:', error);
        //         // Log the specific error message if available
        //         if (error instanceof Error) {
        //             console.error('Error message:', error.message);
        //         }
        //     }
        // },
        async GetPageList() {
            var vm = this;
            var url = `sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&CompaignType=${vm.CampaignType}&status=${vm.status}`;
            DataService.GetSocialMediaPagesList(url).then(function (response) {
                vm.Headers.forEach((column) => {
                    column.settings = null;
                    if (column.COLUMN_NAME == "CAMPAIGN_TYPE_NAME") {
                        column.settings = {
                            isInSlot: true,
                        };
                    }
                });
                vm.AllPages = response.data;
            })
        },
        loginByInstagram() {
            var vm = this;
            window.location.href = "https://api.instagram.com/oauth/authorize?client_id=" + encodeURIComponent(vm.FacebookAppId.toString()) +
                //"&config_id=" + encodeURIComponent(vm.FacebookConfigurationId.toString()) +
                "&redirect_uri=" + encodeURIComponent(vm.FacebookAuthCallBackUrl.toString()) +
                //"&scope=user_profile,user_media" +
                "&response_type=code";
        },
        // async loginByFacebook() {
        //     debugger;
        //     try {
        //     const response = await FB.login();
        //     if (response.authResponse) {
        //         debugger;
        //         // User is logged in, you can now use the access token
        //         const accessToken = response.authResponse.accessToken;
        //         console.log('Access Token:', accessToken);
        //         // Perform additional actions, e.g., send the token to your server
        //     } else {
        //         debugger
        //         console.error('Facebook login failed');
        //     }
        //     } catch (error) {
        //         debugger
        //     console.error('Error during Facebook login:', error);
        //     }
        // }
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Delete':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        actionButtonClick(callbackfunction, item, index, event, isChild) {
            switch (callbackfunction) {
                case 'openQuickViewpopup':
                    //this.DeleteIncidentRow(item.ID);
                    break;
                case 'DeleteCampaign':
                    this.DeleteSocialMediaPages(item.ID);
                    break;
            }
        },
        leftCommonSearch() {

        },
        AddNewPost: function () {
            this.$router.push({ name: 'AddSocialMediaPost' });
        },
        DeleteSocialMediaPages(Page_id) {
            var vm = this;
            var postJSON = {
                ids: Page_id,
                refCode: 'CRM_SOCIAL_PAGES'
            };
            var objectPostString = JSON.stringify(postJSON);
            DataService.CommonDeleteData(objectPostString).then(function (response) {
                var box = "";
                if (response.status == 200) {
                    box = vm.ShowAlert(vm.$t('DeletedSuccessfully', [vm.$t('Page')]), "success", true, vm.$t("Alert"));
                    vm.GetPageList();
                }
                else {
                    box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Page')]), "failure", true, vm.$t("Alert"));
                }
                setTimeout(function () {
                    box.modal('hide');
                }, 2000);
            })
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        }
    }
}
</script>